// src/components/SubscriptionForm.js
import React, { useState } from 'react';
import { db } from '../firebase';
import { ref, push } from 'firebase/database';

const SubscriptionForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await push(ref(db, 'subscriptions'), { name, email });
      setSuccess(true);
      setName('');
      setEmail('');

      // Add a fade-out class to the success message
      setTimeout(() => {
        document.querySelector('.alert-success')?.classList.add('fade-out');
        setTimeout(() => setSuccess(false), 500); // Hide after fade out
      }, 3000);
    } catch (error) {
      setError('Error signing up. Please try again.');
    }
  };

  return (
    <div className="subscription-form-container">
      <div className="subscription-form border p-3 rounded">
        <h2 style={{ fontWeight: 'bold' }}>Don't miss another prop firm deal!</h2>
        <p>Get free email alerts when a new deal is available.</p>
        <form onSubmit={handleSubmit}>
          <div className="form-group mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </div>
          <div className="form-group mb-3">
            <input
              type="email"
              className="form-control"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <button type="submit" className="btn btn-primary w-100">Subscribe</button>
          {success && (
            <div className="alert alert-success mt-3">Thank you for signing up!</div>
          )}
          {error && <div className="alert alert-danger mt-3">{error}</div>}
        </form>
      </div>
    </div>
  );
};

export default SubscriptionForm;
