import React from 'react';
import { Table } from 'react-bootstrap';

const Affiliates = () => {
  const data = [
    { company: "Apex Trader Funding", discount: "90%", resetDiscount: "0%", accFee: "0%", signUpLink: "https://bit.ly/ATFPICKLE", coupon: "👈Click the link👈", dealEnd: "On-going", extra: "1-day to pass!" },
    { company: "Elite Trader Funding", discount: "25%", resetDiscount: "0%", accFee: "0%", signUpLink: "https://tinyurl.com/Picklesir", coupon: "👈Click the link👈", dealEnd: "On-going", extra: "" },
    { company: "TopStep", discount: "20%", resetDiscount: "0%", accFee: "0%", signUpLink: "https://tinyurl.com/SIRPICKLE", coupon: "👈Click the link👈", dealEnd: "On-going", extra: "Earn a funded account on TradingView" },
    { company: "MyFundedFutures", discount: "5%", resetDiscount: "0%", accFee: "0%", signUpLink: "https://myfundedfutures.com/?ref=965", coupon: "👈Click the link👈", dealEnd: "On-going", extra: "" },
  ];

  return (
    <div className="prop-table-container">
      <div className="prop-table-wrapper">
        <Table striped bordered hover className="prop-table">
          <thead>
            <tr>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Company</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Discount</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Reset Discount</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Activation Fee Discount</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Sign Up Link</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Coupon Code</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Deal Ends</th>
              <th style={{ backgroundColor: '#6A7D4E', color: 'white' }}>Extra</th>
            </tr>
          </thead>
          <tbody>
            {data.map((item, index) => (
              <tr key={index}>
                <td>{item.company}</td>
                <td>{item.discount}</td>
                <td>{item.resetDiscount}</td>
                <td>{item.accFee}</td>
                <td><a href={item.signUpLink} target="_blank" rel="noopener noreferrer">{item.signUpLink}</a></td>
                <td>{item.coupon}</td>
                <td>{item.dealEnd}</td>
                <td>{item.extra}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
    </div>
  );
};

export default Affiliates;
